<template>
  <div id="wrapper">
    <Header />
    <main id="main" :class="wrapperClass">
      <slot></slot>
    </main>
    <Footer />
    <div v-if="isClient">
      <CookieBarWrap />
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref } from "vue";
import Header from "@/base/core/components/Header.vue";
import Footer from "@/base/core/components/Footer.vue";
import CookieBarWrap from "@/base/core/components/CookieBar.vue";

const isClient = ref(false);

onMounted(() => {
  isClient.value = true;
});

defineProps({
  wrapperClass: {
    type: String,
    required: false,
  },
});
</script>
